import { Pipe, PipeTransform, OnDestroy } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { RufLocaleService } from './locale.service';

@Pipe({
  name: 'rufCurrency',
  pure: false
})
export class RufCurrencyPipe implements PipeTransform, OnDestroy {
  private currencyPipe;
  private subscription;

  constructor(private localeService: RufLocaleService) {
    this.subscription = localeService.locale$.subscribe(data => {
      this.currencyPipe = new CurrencyPipe(data);
    });
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  transform(value: number, currencyCode: string, symbolDisplay: boolean, digits: number): number {
    return this.currencyPipe.transform(value, currencyCode, symbolDisplay, digits);
  }
}
