import {
  Component,
  EventEmitter,
  HostListener,
  TemplateRef,
  ViewEncapsulation,
  Input,
  Output,
  ViewChild,
  ViewContainerRef,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import { TemplatePortal } from '@angular/cdk/portal';
import { ENTER, SPACE, ESCAPE } from '@angular/cdk/keycodes';
import { Overlay,
         OverlayConnectionPosition,
         OriginConnectionPosition,
         FlexibleConnectedPositionStrategy,
         OverlayRef,
         CdkOverlayOrigin,
         HorizontalConnectionPos,
         VerticalConnectionPos} from '@angular/cdk/overlay';

import { RufTriggerTarget } from '../trigger/trigger.interface';

export enum RufDropdownType {
  PopoverMenu = 'popover-menu',
  ContextMenu = 'context-menu',
  Dropdown = 'dropdown',
};

@Component({
  selector: 'ruf-dropdown-panel',
  templateUrl: './dropdown-panel.component.html',
  styleUrls: ['dropdown-panel.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class RufDropdownPanelComponent implements RufTriggerTarget, OnChanges {
  @Input() origin: CdkOverlayOrigin;
  @Input() opened: boolean;
  @Input() hasBackdrop = true;
  @Output() openedChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Input() type = RufDropdownType.Dropdown;
  @Input() xPosition = 'center';
  @Input() yPosition = 'bottom';
  className = 'ruf-dropdown-panel-container';
  @Input() closeIcon: boolean;
  private _overlayRef: OverlayRef;
  private _portal: TemplatePortal<any>;
  @ViewChild(TemplateRef, {static: false}) content: TemplateRef<any>;
  @Output() toggle = new EventEmitter<boolean>();

  constructor(private _overlay: Overlay,
              private _viewContainerRef: ViewContainerRef) {}

 ngOnChanges(changes: SimpleChanges): void {
  if (changes &&
      changes.hasBackdrop &&
      changes.hasBackdrop.previousValue !== changes.hasBackdrop.currentValue) {
    // when value is changes for hasBackdrop set value null for overlay
    // so that it will create new overlay with updated backdrop value
    if (this._overlayRef) {
      this._overlayRef.detach();
      this._overlayRef = null;
    }
  }
 }

  open() {
    const origin = this._getOrigin();
    const overlay = this._getOverlayPosition();

    if (!this._overlayRef) {
      let positionStrategy: FlexibleConnectedPositionStrategy;
      positionStrategy = this._overlay.position()
        .flexibleConnectedTo(this.origin.elementRef)
        .withPositions([
          { ...origin.main, ...overlay.main },
          {...origin.fallback, ...overlay.fallback}
        ]);

      this.onPositionChanged({
        connectionPair: {
          overlayX: overlay.main.overlayX,
          overlayY: overlay.main.overlayY
        }
      });

      positionStrategy.positionChanges.subscribe(change => {
        this.onPositionChanged(change);
      });

      this._overlayRef = this._overlay.create({
        hasBackdrop: this.hasBackdrop,
        backdropClass: 'ruf-dropdown-panel-wrapper',
        positionStrategy
      });

      this._overlayRef.backdropClick().subscribe(() => this.close());
      this._portal = new TemplatePortal(this.content, this._viewContainerRef);
    }

    if (!this._overlayRef.hasAttached()) {
      this._overlayRef.attach(this._portal);
      this.opened = true;
      this.toggle.emit(true);
    } else {
      this.close();
      this.toggle.emit(false);
    }
  };

  _getOrigin(): { main: OriginConnectionPosition, fallback: OriginConnectionPosition} {
    let originPosition: OriginConnectionPosition;
    originPosition = this._getPositionStrategy(this._getOriginValues(), this._getOverlayPositionValues()).originPosition;
    const {x, y} = this._invertPosition(originPosition.originX, originPosition.originY);
    if (this.type === RufDropdownType.PopoverMenu || this.type === RufDropdownType.ContextMenu) {
      return {
        main: originPosition,
        fallback: {originX: x, originY: y}
      }
    } else {
      return {
        main: {originX: 'start', originY: 'bottom'},
        fallback: {originX: 'end', originY: 'top'}
      }
    }
  }

  _getOverlayPosition(): { main: OverlayConnectionPosition, fallback: OverlayConnectionPosition} {
    const XPosition = this.xPosition;
    const YPosition = this.yPosition;
    let overlayPosition: OverlayConnectionPosition;
    overlayPosition = this._getPositionStrategy(this._getOriginValues(), this._getOverlayPositionValues()).overlayPosition;
    const {x, y} = this._invertPosition(overlayPosition.overlayX, overlayPosition.overlayY);
    if (this.type === RufDropdownType.PopoverMenu || this.type === RufDropdownType.ContextMenu) {
      return {
        main: overlayPosition,
        fallback: {overlayX: x, overlayY: y}
      }
    } else {
      return {
        main: {overlayX: 'start', overlayY: 'top'},
        fallback: {overlayX: 'end', overlayY: 'bottom'}
      }
    }
  }

  _getOriginValues(): { xCenterYBottom: OriginConnectionPosition, xCenterYTop: OriginConnectionPosition
                  xStartYBottom: OriginConnectionPosition, xStartYTop: OriginConnectionPosition, xStartYCenter: OriginConnectionPosition,
                  xEndYBottom: OriginConnectionPosition, xEndYTop: OriginConnectionPosition, xEndYCenter: OriginConnectionPosition} {
    const xCenterYBottom: OriginConnectionPosition = {originX: 'center', originY: 'bottom'};
    const xCenterYTop: OriginConnectionPosition = {originX: 'center', originY: 'top'};
    const xStartYBottom: OriginConnectionPosition = {originX: 'start', originY: 'bottom'};
    const xStartYTop: OriginConnectionPosition = {originX: 'start', originY: 'top'};
    const xStartYCenter: OriginConnectionPosition = { originX: 'start', originY: 'center'};
    const xEndYBottom: OriginConnectionPosition = {originX: 'end', originY: 'bottom'};
    const xEndYTop: OriginConnectionPosition = {originX: 'end', originY: 'top'};
    const xEndYCenter: OriginConnectionPosition = { originX: 'end', originY: 'center'};

    return {
      xCenterYBottom: xCenterYBottom,
      xCenterYTop: xCenterYTop,
      xStartYBottom: xStartYBottom,
      xStartYTop: xStartYTop,
      xStartYCenter: xStartYCenter,
      xEndYBottom: xEndYBottom,
      xEndYTop: xEndYTop,
      xEndYCenter: xEndYCenter
    };
  }

  _getOverlayPositionValues(): {
          xCenterYTop: OverlayConnectionPosition, xCenterYBottom: OverlayConnectionPosition
          xStartYTop: OverlayConnectionPosition, xStartYBottom: OverlayConnectionPosition, xStartYCenter: OverlayConnectionPosition,
          xEndYTop: OverlayConnectionPosition, xEndYBottom: OverlayConnectionPosition, xEndYCenter: OverlayConnectionPosition} {
      const xCenterYTop: OverlayConnectionPosition = {overlayX: 'center', overlayY: 'top'};
      const xCenterYBottom: OverlayConnectionPosition = {overlayX: 'center', overlayY: 'bottom'};
      const xStartYTop: OverlayConnectionPosition = {overlayX: 'start', overlayY: 'top'};
      const xStartYBottom: OverlayConnectionPosition = {overlayX: 'start', overlayY: 'bottom'};
      const xStartYCenter: OverlayConnectionPosition = {overlayX: 'start', overlayY: 'center'};
      const xEndYTop: OverlayConnectionPosition = {overlayX: 'end', overlayY: 'top'};
      const xEndYBottom: OverlayConnectionPosition = {overlayX: 'end', overlayY: 'bottom'};
      const xEndYCenter: OverlayConnectionPosition = {overlayX: 'end', overlayY: 'center'};

      return {
        xCenterYTop: xCenterYTop,
        xCenterYBottom: xCenterYBottom,
        xStartYTop: xStartYTop,
        xStartYBottom: xStartYBottom,
        xStartYCenter: xStartYCenter,
        xEndYTop: xEndYTop,
        xEndYBottom: xEndYBottom,
        xEndYCenter: xEndYCenter
      };
  }

  _getPositionStrategy(origin, overlay) {
    let originPosition = origin.xCenterYBottom;
    let overlayPosition = overlay.xCenterYTop;
    if (this.xPosition === 'start') {
      if (this.yPosition === 'top') {
        originPosition = origin.xStartYTop;
        overlayPosition = overlay.xStartYBottom;
      } else if (this.yPosition === 'center' && this.type === RufDropdownType.PopoverMenu) {
        originPosition = origin.xStartYCenter;
        overlayPosition = overlay.xEndYCenter;
      } else if (this.yPosition === 'bottom' || (this.yPosition === 'center' && this.type === RufDropdownType.ContextMenu)) {
        originPosition = origin.xStartYBottom;
        overlayPosition = overlay.xStartYTop;
      }
    } else if (this.xPosition === 'end') {
      if (this.yPosition === 'top') {
        originPosition = origin.xEndYTop;
        overlayPosition = overlay.xEndYBottom;
      } else if (this.yPosition === 'center' && this.type === RufDropdownType.PopoverMenu) {
        originPosition = origin.xEndYCenter;
        overlayPosition = overlay.xStartYCenter;
      } else if (this.yPosition === 'bottom' || (this.yPosition === 'center' && this.type === RufDropdownType.ContextMenu)) {
        originPosition = origin.xEndYBottom;
        overlayPosition = overlay.xEndYTop;
      }
    } else if (this.xPosition === 'center') {
      originPosition = this.yPosition === 'top' ? origin.xCenterYTop : origin.xCenterYBottom;
      overlayPosition = this.yPosition === 'top' ? overlay.xCenterYBottom : overlay.xCenterYTop;
    }
    return {
      originPosition: originPosition,
      overlayPosition: overlayPosition
    }
  }

  _invertPosition(x: HorizontalConnectionPos, y: VerticalConnectionPos) {
    if (x === 'start') {
      x = 'end'
    } else if (x === 'end') {
      x = 'start'
    }

    if (y === 'top') {
      y = 'bottom';
    } else if (y === 'bottom') {
      y = 'top';
    }
    return {x, y};
  }

  onPositionChanged ($event) {
    if (this.type === RufDropdownType.PopoverMenu || this.type === RufDropdownType.ContextMenu) {
      if ($event.connectionPair.overlayY === 'top') {
        if ($event.connectionPair.overlayX === 'center') {
          this.className = 'ruf-dropdown-panel-container ruf-popover-menu center-arrow-up';
        } else if ($event.connectionPair.overlayX === 'start') {
          this.className = 'ruf-dropdown-panel-container ruf-popover-menu left-arrow-up';
        } else {
          this.className = 'ruf-dropdown-panel-container ruf-popover-menu right-arrow-up';
        }
      } else if ($event.connectionPair.overlayY === 'center') {
        if ($event.connectionPair.overlayX === 'start') {
          this.className = 'ruf-dropdown-panel-container ruf-popover-menu right-arrow-center';
        } else {
          this.className = 'ruf-dropdown-panel-container ruf-popover-menu left-arrow-center';
        }
      } else {
        if ($event.connectionPair.overlayX === 'center') {
          this.className = 'ruf-dropdown-panel-container ruf-popover-menu center-arrow-down';
        } else if ($event.connectionPair.overlayX === 'start') {
          this.className = 'ruf-dropdown-panel-container ruf-popover-menu left-arrow-down';
        } else {
          this.className = 'ruf-dropdown-panel-container ruf-popover-menu right-arrow-down';
        }
      }
    } else if (this.type === RufDropdownType.Dropdown) {
      this.className = 'ruf-dropdown-panel-container';
    }
  }

  close() {
    if (this.opened) {
      this.opened = false;
      this._overlayRef.detach();
      this.openedChange.emit(this.opened);
    }
  }

  hasPopup(): boolean {
    return true;
  }

  handleKeydown(event) {
    switch (event.keyCode) {
      case SPACE:
      case ENTER:
        this.open();
        event.preventDefault();
        event.stopPropagation();
        this.toggle.emit(true);
        break;
      case ESCAPE:
        this.close();
        this.toggle.emit(false);
        event.stopPropagation();
        break;
      default:
    }
  }

  @HostListener('click', ['$event'])
  handleClick(event) {
    this.open();
  }

}
