import { Pipe, PipeTransform, OnDestroy } from '@angular/core';
import { DatePipe } from '@angular/common';
import { RufLocaleService } from './locale.service';

@Pipe({
  name: 'rufDate',
  pure: false
})
export class RufDatePipe implements PipeTransform, OnDestroy {
  private datePipe: DatePipe;
  private subscription;

  constructor(private localeService: RufLocaleService) {
    this.subscription = localeService.locale$.subscribe(data => {
      this.datePipe = new DatePipe(data);
    });
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  transform(value: string, pattern?: string): string {
    return this.datePipe.transform(value, pattern);
  }
}
