import {
  Component,
  ViewEncapsulation,
  ChangeDetectorRef,
  ChangeDetectionStrategy,
  Input,
  Output,
  EventEmitter,
  OnDestroy
} from '@angular/core';

import { Subscription } from 'rxjs';
import { RufShellIntl } from '../utils/shell-intl';

@Component({
  selector: 'ruf-side-panel',
  styleUrls: ['./side-panel.component.scss'],
  templateUrl: './side-panel.component.html',
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})

export class RufSidePanelComponent implements OnDestroy {

  private _intlChanges: Subscription;
  private _pinLabel: string;
  private _unpinLabel: string;

  /* tslint:disable:no-input-rename */
  @Input('pinned') isPinned = false;
  @Output() togglePin = new EventEmitter();

  @Input()
  get pinLabel() {
    return this._pinLabel || this._intl.sidepanel.aria.pinLabel;
  }
  set pinLabel(value: string) {
    this._pinLabel = value;
  }

  @Input()
  get unpinLabel() {
    return this._unpinLabel || this._intl.sidepanel.aria.unpinLabel;
  }
  set unpinLabel(value: string) {
    this._unpinLabel = value;
  }

  get toggleLabel() {
    return this.isPinned ? this.unpinLabel : this.pinLabel;
  }

  constructor( private _intl: RufShellIntl,
               private changeDetectorRef: ChangeDetectorRef
              ) {
         this._intlChanges = _intl.changes.subscribe(() => {
         changeDetectorRef.markForCheck();
      });
   }

 onPintoggle($event) {
   this.isPinned = !this.isPinned;
   this.togglePin.emit({'pinned': this.isPinned});
 }

  ngOnDestroy() {
    this._intlChanges.unsubscribe();
  }
}
