import { Injectable } from '@angular/core';
import { Observable, from, BehaviorSubject } from 'rxjs';

@Injectable()
export class RufLocaleService {
  private _locale = 'en';
  private _localeSubject: BehaviorSubject<string> = new BehaviorSubject(this._locale);
  private _locale$: Observable<string> = from(this._localeSubject);

  set locale(locale: string) {
    this._locale = locale;
    this._localeSubject.next(locale);
  }

  get locale(): string {
    return this._locale;
  }

  get locale$(): Observable<string> {
    return this._locale$;
  }
}
