import {
  ChangeDetectorRef,
  ChangeDetectionStrategy,
  Component,
  Input,
  OnDestroy,
  Output,
  HostBinding,
  ViewEncapsulation,
  EventEmitter,
  AfterContentInit,
  ViewChild,
  ElementRef
} from '@angular/core';
import {
  trigger,
  state,
  style,
  transition,
  animate
} from '@angular/animations';
import { Subscription } from 'rxjs';
import { RufShellIntl } from '../utils/shell-intl';

@Component({
  selector: 'ruf-statusbar',
  templateUrl: './statusbar.component.html',

  styleUrls: ['./statusbar.component.scss'],
  animations: [trigger('animation', [
    state('0', style({ height: '0', 'min-height': '0', visibility: 'hidden'})),
    state('1', style({ height: '*', 'min-height': '*', visibility: 'visible'})),
    transition('0 => 1', animate('250ms ease-in')),
    transition('1 => 0', animate('250ms ease-out'))
    ]), trigger('expanded', [
    state('false', style({ height: 0, visibility: 'hidden' })),
    state('true', style({ height: '*', visibility: 'visible'})),
    transition('false => true', animate('300ms ease-in-out')),
    transition('true => false', animate('300ms ease-in-out'))
  ])],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class RufStatusbarComponent implements OnDestroy, AfterContentInit {
  private _intlChanges: Subscription;
  private _closeAriaLabel: string;
  private _expandAriaLabel: string;

  @Input() title: string;

  @Input() color = 'accent';
  @Input() closeable = true;
  @Input() expandable = false;
  @Input() icon: string;
  @Input() border = true;
  @ViewChild('subtitleRef', {static: true}) subtitleContent: ElementRef<any>;
  isSubtitleEmpty = false;

  @HostBinding('@animation')
  @Input()
  visible = true;

  @Input()
  get closeAriaLabel() {
    return this._closeAriaLabel || this._intl.statusbar.aria.closeIconLabel;
  }

  set closeAriaLabel(value: string) {
    this._closeAriaLabel = value;
  }

  @Input()
  get expandAriaLabel() {
    return this._expandAriaLabel || this._intl.statusbar.aria.expandIconLabel;
  }

  set expandAriaLabel(value: string) {
    this._expandAriaLabel = value;
  }

  @HostBinding('class')
  get _class() {
    const colorClass =  this.color ? `ruf-${this.color}` : 'ruf-accent';
    const borderClass =  !this.border ? `ruf-statusbar-noborder` : '';
    return `${colorClass} ${borderClass}`;
  }

  @Output() dismiss = new EventEmitter();

  constructor(private _intl: RufShellIntl, changeDetectorRef: ChangeDetectorRef) {
    this._intlChanges = _intl.changes.subscribe(() => changeDetectorRef.markForCheck());
  }

  onDismiss($event) {
    this.dismiss.emit();
  }

  ngOnDestroy() {
    this._intlChanges.unsubscribe();
  }

  ngAfterContentInit() {
    this.isSubtitleEmpty = this.subtitleContent.nativeElement.innerHTML.trim() === '';
  }
}


@Component({
  selector: 'ruf-statusbar-icon',
  encapsulation: ViewEncapsulation.None,
  template: `
    <div class="ruf-statusbar-icon"><ng-content></ng-content></div>
  `
})
export class RufStatusbarIconComponent {}

@Component({
  selector: 'ruf-statusbar-description',
  encapsulation: ViewEncapsulation.None,
  template: `
   <div class="ruf-statusbar-description"><ng-content></ng-content></div>
  `
})
export class RufStatusbarDescriptionComponent {}

