import {
  ChangeDetectorRef,
  Component,
  Input,
  ViewEncapsulation,
  Directive,
  ContentChild,
  ChangeDetectionStrategy,
  HostBinding,
  OnDestroy
} from '@angular/core';

import { Subscription } from 'rxjs';
import { RufShellIntl } from '../utils/shell-intl';

@Directive({
  selector: 'ruf-banner-row' // tslint:disable-line
})
export class RufBannerRowDirective {}

@Directive({
  selector: '[rufBannerControls]' // tslint:disable-line
})
export class RufBannerControlsDirective {}

@Component({
  selector: 'ruf-banner-brand',
  template: `
    <div rufId [class]="'ruf-banner-brand-logo ' + (!logoUrl && logoClass)" *ngIf="showLogo" >
      <span hidden="true" aria-hidden="false" [attr.aria-label]="ariaLabel"
      [attr.aria-labelledby]="ariaLabelledby">{{ariaLabel}}</span>
      <img rufId *ngIf="logoUrl" [src]="logoUrl" [attr.alt]="altText">
    </div>
    <div rufId class="ruf-toolbar-separator" *ngIf="title && showLogo"></div>
    <h1 *ngIf="title" class="ruf-banner-brand-title">{{title}}</h1>
  `,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RufBannerBrandComponent implements OnDestroy {
  private _intlChanges: Subscription;
  private _altText: string;
  private _ariaLabel: string;
  private _ariaLabelledby: string;
  @Input() logoUrl: string;
  @Input() title: string;
  @Input() logoClass = 'fis-logo';
  @Input() showLogo = true;

  @Input()
  get altText() {
    return this._altText || this._intl.banner.altText;
  }
  set altText(value: string) {
    this._altText = value;
  }

  @Input()
  get ariaLabel() {
    return this._ariaLabel || this._intl.banner.aria.label;
  }
  set ariaLabel (value: string) {
    this._ariaLabel = value;
  }

  @Input()
  get ariaLabelledby() {
    return this._ariaLabelledby || this._intl.banner.aria.labelledBy;
  }
  set ariaLabelledby (value: string) {
    this._ariaLabel = value;
  }

  constructor(private _intl: RufShellIntl, changeDetectorRef: ChangeDetectorRef) {
    this._intlChanges = _intl.changes.subscribe(() => changeDetectorRef.markForCheck());
  }

  ngOnDestroy() {
    this._intlChanges.unsubscribe();
  }
}

@Component({
  selector: 'ruf-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class RufBannerComponent {
  @Input() color: string;
  @ContentChild(RufBannerControlsDirective, {static: false}) controls: RufBannerControlsDirective;

  constructor() {}
}
