import { MatButton } from '@angular/material/button';
import { Input, HostBinding, Directive, Self, Optional } from '@angular/core';

@Directive({
  selector: '[rufIconButton]',
  exportAs: 'rufIconButton'
})
export class RufIconButtonDirective {
  private _active = false;
  private _dark = false;
  private _activeClass = 'activeClass';

  @Input() fontSet = 'fisfont';
  @Input() fontIcon: string;

  // can be used in a container that supports the ruf-square class
  @HostBinding('class.ruf-square') square = true;
  @HostBinding('class.ruf-background-dark') darkClass = this._dark;
  @HostBinding('class.ruf-background-darker') activeClass = false;
  @HostBinding('class.ruf-background-primary-dark') primaryClass = false;

  @HostBinding('style.transition') transition = 'background-color 1s ease';

  @HostBinding('attr.tabindex') tabindex = 0;

  constructor(
    @Self()
    @Optional()
    _matButton: MatButton
  ) {
    if (_matButton) {
      _matButton.disableRipple = true;
    }
  }

  @Input()
  set activeColor(color) {
    if (color === 'primary') {
      this._activeClass = 'primaryClass';
    }
  }

  @Input()
  set active(isActive: boolean) {
    this._active = !!isActive;
    this[this._activeClass] = this._active;
  }

  get active() {
    return this._active;
  }

  @Input()
  set dark(isActive) {
    this._dark = isActive;
    this.darkClass = <any>isActive === '' || isActive;
  }

  get dark() {
    return this._dark;
  }
}
