import {
    Directive,
    Input,
    HostBinding,
    Renderer2,
    ElementRef,
    AfterViewInit
} from '@angular/core';

@Directive({
    selector: '[rufInkbar]',
    exportAs: 'rufInkbar'
})

export class RufInkbarDirective {
    private _color = 'primary';
    private _position = 'bottom';
    @HostBinding('class.ruf-inkbar') rufInkbar = 'ruf-inkbar';

    @Input()
    get inkbarPosition() {
        return this._position;
    }
    set inkbarPosition(value: string) {
        const position = value || 'bottom';
        this._updateClass(position, this._position);
        this._position = position;
    }

    @Input()
    get inkbarColor() {
        return this._color;
    }

    set inkbarColor(value: string) {
        const color = value || 'primary'
        this._updateClass(color, this._color);
        this._color = color;
    }

    constructor(private renderer: Renderer2, private elm: ElementRef) {
        // add default classes;
        this._updateClass(this._color, null);
        this._updateClass(this._position, null);
    }

    // method to update the class based on the input
    private _updateClass(newValue, oldValue) {
        if (oldValue) {
            // remove class only if newValue is not null
            this.renderer.removeClass(this.elm.nativeElement, `ruf-inkbar-${oldValue}`);
        }
        if (newValue) {
            this.renderer.addClass(this.elm.nativeElement, `ruf-inkbar-${newValue}`);
        }
    }
}


@Directive({
    selector: '[rufInkbarActive]',
    exportAs: 'rufInkbarActive'
})

export class RufInkbarActiveDirective {
    @HostBinding('class.ruf-inkbar-item') rufInkbarItem = 'ruf-inkbar-item';
    @HostBinding('class.ruf-inkbar-active')
    get _inkbarActive(): boolean {
        return this.rufInkbarActive;
    }
    @Input() rufInkbarActive: boolean;
}
