import {
  ChangeDetectorRef,
  ChangeDetectionStrategy,
  Component,
  Directive,
  HostBinding,
  ViewEncapsulation,
  Input,
  ContentChild,
  OnDestroy,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { RufShellIntl } from '../utils/shell-intl';

@Directive({
  selector: 'ruf-dialog-header' // tslint:disable-line
})
export class RufDialogHeaderDirective {}

@Directive({
  selector: 'ruf-dialog-content' // tslint:disable-line
})
export class RufDialogContentDirective {}

@Directive({
  selector: 'ruf-dialog-footer' // tslint:disable-line
})
export class RufDialogFooterDirective {}

@Component({
  selector: 'ruf-dialog-panel',
  templateUrl: 'dialog-panel.component.html',
  styleUrls: ['dialog-panel.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RufDialogPanelComponent implements OnDestroy {
  private _intlChanges: Subscription;
  private _closeIconTitle: string;
  @ContentChild(RufDialogFooterDirective, {static: false}) footer: RufDialogFooterDirective;
  @HostBinding('attr.tabindex') tabindex = 0;

  @Input() separator = false;
  @Input()
  get closeIconTitle() {
    return this._closeIconTitle || this._intl.dialog.closeIconTitle;
  }
  set closeIconTitle(value: string) {
    this._closeIconTitle = value;
  }

  constructor(private _intl: RufShellIntl, changeDetectorRef: ChangeDetectorRef) {
    this._intlChanges = _intl.changes.subscribe(() => changeDetectorRef.markForCheck());
  }
  ngOnDestroy() {
     this._intlChanges.unsubscribe();
  }

}
