import {
  Component,
  ViewEncapsulation,
  ChangeDetectionStrategy,
  ElementRef,
  Renderer2,
  ContentChildren,
  QueryList,
  forwardRef,
  HostBinding
} from '@angular/core';
import { Observable } from 'rxjs';
import { RufMenubarComponent } from '../menubar/menubar.component';
import { RufSidemenuItemComponent } from './sidemenu-item.component';

@Component({
  selector: 'ruf-sidemenu',
  templateUrl: '../menubar/menubar.component.html',
  styleUrls: ['./sidemenu.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [RufMenubarComponent],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RufSidemenuComponent extends RufMenubarComponent {
  direction = 'column';
  inkbarPosition = 'start';

  @HostBinding('attr.aria-orientation') orientation = 'vertical';

  @ContentChildren(forwardRef(() => RufSidemenuItemComponent))
  menuItems: QueryList<RufSidemenuItemComponent>;

  constructor(_elementRef: ElementRef, _renderer: Renderer2) {
    super(_elementRef, _renderer);
  }
}
