import { Directive, HostBinding, Input, OnChanges, SimpleChanges, Renderer2, ElementRef } from '@angular/core';

export const RUF_MARGIN_VALUES = ['none', 'auto', 'small', 'medium', 'large'];

@Directive({
  selector: `
    [rufMargin],
    [rufMarginVertical],
    [rufMarginHorizontal],
    [rufMarginTop],
    [rufMarginRight],
    [rufMarginBottom],
    [rufMarginLeft]
  `,
  exportAs: 'rufMargin'
})
export class RufMarginDirective implements OnChanges {
  private classPrefix = 'ruf-margin';

  private _positions = {
    rufMargin: ['-'],
    rufMarginVertical: ['-top-', '-bottom-'],
    rufMarginHorizontal: ['-left-', '-right-'],
    rufMarginTop: ['-top-'],
    rufMarginRight: ['-right-'],
    rufMarginBottom: ['-bottom-'],
    rufMarginLeft: ['-left-']
  };

  @Input() rufMargin = '';
  @Input() rufMarginVertical = '';
  @Input() rufMarginHorizontal = '';
  @Input() rufMarginTop = '';
  @Input() rufMarginRight = '';
  @Input() rufMarginBottom = '';
  @Input() rufMarginLeft = '';

  @HostBinding('class.ruf-margin') margin = true;

  constructor(private el: ElementRef, private renderer: Renderer2) {}

  ngOnChanges(changes: SimpleChanges) {
    Object.keys(changes).forEach(key => {
      this.update(key, changes[key]);
    });
  }

  private update(key, change) {
    const previous = this._validateValue(change.previousValue);
    const current = this._validateValue(change.currentValue);
    const positions = this._positions[key];

    positions.forEach(pos => {
      // remove
      this.renderer.removeClass(this.el.nativeElement, this.classPrefix + pos + previous);
      // add
      this.renderer.addClass(this.el.nativeElement, this.classPrefix + pos + current);
    });
  }

  private _validateValue(val) {
    if (RUF_MARGIN_VALUES.indexOf(val) !== -1) {
      return val;
    }
    return 'medium';
  }
}
