import { ElementRef } from '@angular/core';
import { ThemePalette } from '@angular/material/core';

export type Constructor<T> = new(...args: any[]) => T;

export interface RufCanColor {
  color: RufThemePalette
}

export interface HasElementRef {
  _elementRef: ElementRef;
}

export type RufThemePalette = ThemePalette & 'emphasis' | 'info' | 'success' | 'error';

export type RufCanColorCtor = Constructor<RufCanColor>;

export function rufMixinColor<T extends Constructor<HasElementRef>>(base: T, defaultColor?: RufThemePalette): RufCanColorCtor & T {
  return class extends base {
    private _color: RufThemePalette;

    constructor(...args: any[]) {
      super(...args);

      // Set the default color that can be specified from the mixin.
      this.color = defaultColor;
    }

    get color(): RufThemePalette {
      return this._color;
    }

    set color(value: RufThemePalette) {
      const colorPalette = value || defaultColor;

      if (colorPalette !== this._color) {
        if (this._color) {
          this._elementRef.nativeElement.classList.remove(`ruf-${this._color}`);
        }
        if (colorPalette) {
          this._elementRef.nativeElement.classList.add(`ruf-${colorPalette}`);
        }

        this._color = colorPalette;
      }
    }
  };
}

export class RufElementRefBase {
  constructor(public _elementRef: ElementRef) {}
}

export const RufMixinBase: RufCanColorCtor & typeof RufElementRefBase = rufMixinColor(RufElementRefBase);


