import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RufRouterOutletDirective } from './outlet.directive';
import { RufRouterUtilService } from './router-util.service';

@NgModule({ declarations: [RufRouterOutletDirective], imports: [CommonModule], exports: [RufRouterOutletDirective] })
export class RufRouterModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: RufRouterModule,
      providers: [RufRouterUtilService]
    };
  }
}
