import {
  Component,
  ViewEncapsulation,
  ChangeDetectionStrategy,
  ElementRef,
  Renderer2,
  EventEmitter,
  Input,
  Inject,
  Output,
  ViewChild,
  HostBinding,
  ViewChildren,
  QueryList
} from '@angular/core';
import { MediaObserver } from '@angular/flex-layout';

import { RufDynamicSidemenuItem } from './dynamic-sidemenu-item';
import { MatMenuTrigger } from '@angular/material/menu';
import {DOCUMENT} from '@angular/common';
import { ENTER, SPACE } from '@angular/cdk/keycodes';
import { RufMenubarItem } from '../menubar//menubar-item.component';
import { RufToggleDirective } from '../toggle/toggle.directive';

@Component({
  selector: 'ruf-dynamic-sidemenu',
  templateUrl: './dynamic-sidemenu.component.html',
  styleUrls: ['./dynamic-sidemenu.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class RufDynamicSideMenuComponent {
  @Input() items: RufDynamicSidemenuItem[] = [];
  @Input() popupBackgroundClass: string
  sideMenuPopupData: RufDynamicSidemenuItem;

  @ViewChild(MatMenuTrigger, {static: false}) public menuTrigger: MatMenuTrigger;
  @ViewChild('menuTrigger', {static: false}) public trigger: ElementRef;

  /* tslint:disable:no-input-rename */
  /* tslint:disable:no-output-rename */

  @Input('selectedPath') _selectedPath: string;

  @Input('activeColor') _activeColor: string;

  @Input('inkbarPosition') _inkbarPosition = this._inkbarPosition || 'start';

  @Input('multi') multi = true;

  @Output('select') _select = new EventEmitter<RufDynamicSidemenuItem>();
  @ViewChildren('trigger') private _triggers: QueryList<RufToggleDirective>;

  // @HostBinding('class.ruf-dynamic-sidemenu-minimized')
  @Input() minimized = false;
  @Input('minimized.xs') minimizedXs = false;
  @Input('minimized.sm') minimizedSm = false;
  @Input('minimized.md') minimizedMd = false;
  @Input('minimized.lg') minimizedLg = false;
  @Input('minimized.xl') minimizedXl = false;

  @Input('minimized.gt-xs') minimizedGtXs = false;
  @Input('minimized.gt-sm') minimizedGtSm = false;
  @Input('minimized.gt-md') minimizedGtMd = false;
  @Input('minimized.gt-lg') minimizedGtLg = false;


  constructor(
   @Inject(DOCUMENT) private document,
   private _renderer: Renderer2,
   private _mediaObserver: MediaObserver,
   private _elementRef: ElementRef) {

  }


  isMinimized() {
    return [
       this.minimized,
       this.minimizedXs && this._mediaObserver.isActive('xs'),
       this.minimizedSm && this._mediaObserver.isActive('sm'),
       this.minimizedMd && this._mediaObserver.isActive('md'),
       this.minimizedLg && this._mediaObserver.isActive('lg'),
       this.minimizedXl && this._mediaObserver.isActive('xl'),
       this.minimizedGtXs && this._mediaObserver.isActive('gt-xs'),
       this.minimizedGtSm && this._mediaObserver.isActive('gt-sm'),
       this.minimizedGtMd && this._mediaObserver.isActive('gt-md'),
       this.minimizedGtLg && this._mediaObserver.isActive('gt-lg')
      ].some(x => x === true);
  }


  onSelect($event: RufMenubarItem) {
    if (!($event.data && $event.data.children)) {
      this._selectedPath = $event.path;
      this._select.emit($event.data);
    } else if ($event && $event.event) {
      $event.event.preventDefault();
      $event.event.stopPropagation();
    }
  }

  getRufId(item): string { // return id for sidemenu items.
   return 'dynamic_sidemenu_' + item.label.toLowerCase().replace(/ /g, '_');
  }

  closeMenu() {
    this.menuTrigger.closeMenu();
  }

  setPopupData(event, data) {
    this.sideMenuPopupData = data
    event.currentTarget.classList.add('ruf-active')
    // focus on the first menu item
    setTimeout(() => {
      const cdkOverlayClass = `.ruf-sidemenu-popup-backdrop + .cdk-overlay-connected-position-bounding-box`;
      const cdkOverlayElement: HTMLElement = this.document.querySelector(cdkOverlayClass);
      if (cdkOverlayElement) {
        // set left position for cdkOverlay based on the position of menuitem.
        // so we need to get left and width value from BoundingClientRect;
        const rufMenuItem = this._elementRef.nativeElement.querySelector('.ruf-menu-item');
        const rufMenuItemClientTect =   rufMenuItem.getBoundingClientRect();
        cdkOverlayElement.style.left = rufMenuItemClientTect.left + rufMenuItemClientTect.width + 'px';
        const firstMenuItem: HTMLElement = this.document.querySelector(`${cdkOverlayClass} .ruf-menu-item`);
        if (firstMenuItem) {
          firstMenuItem.focus();
        }
      }
    });
  }

  onPopupClose() {
    // remove active class when popop is closed
    const currentActiveItem  = this._elementRef.nativeElement.querySelector('.ruf-menu-item.ruf-active');
    if (currentActiveItem) {
      currentActiveItem.focus();
      this._renderer.removeClass(currentActiveItem, 'ruf-active');
    }
  }

  keyHandler(event) {
    switch (event.keyCode) {
      case ENTER:
      case SPACE:
        event.currentTarget.click();
        break;
      default:
    }
  }

  getOverlayClasses() {
    const colorClass =  this._activeColor ? `ruf-${this._activeColor}` : ``;
    return `${this.popupBackgroundClass} ${colorClass} ruf-menubar-inkbar-${this._inkbarPosition}`;
  }

  toggleChildItems(currentTrigger: RufToggleDirective): void {
    if (this.multi === false) {
      this._triggers.toArray()
      .filter((element: RufToggleDirective) => element.value === true && element !== currentTrigger)
      .forEach((element: any) => {
        element.value = false;
      });
    }
  }
}
