import { RufMenubarComponent } from './menubar.component';
import {
  Component,
  ViewEncapsulation,
  ChangeDetectionStrategy,
  Input,
  AfterContentInit,
  OnDestroy,
  HostBinding,
  HostListener,
  ElementRef
} from '@angular/core';
import { FocusableOption } from '@angular/cdk/a11y'; // Focusable interface renamed to FocusableOption in @angular/cdk beta-10
import { RufBaseItem } from '../common/base-item';
import { Subject } from 'rxjs';
import { distinctUntilChanged, takeUntil } from 'rxjs/operators';

export interface RufMenubarItem extends RufBaseItem {
  data?: any;
  event?: any;
}

@Component({
  selector: 'ruf-menubar-item, [rufMenubarItem]',
  template: `
      <ng-content></ng-content>
  `,
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RufMenubarItemComponent implements RufMenubarItem, FocusableOption, AfterContentInit, OnDestroy {
  private unsubscribe = new Subject();

  @Input() path: string;
  @Input() data: any;
  @Input() selectable = true;

  @HostBinding('class.ruf-menu-item') menuitem = true;
  @HostBinding('attr.role') role = 'menuitem';
  @HostBinding('attr.tabindex') tabindex = -1;
  @HostBinding('class.ruf-menubar-item-active') active = false;

  ngAfterContentInit() {
    this.menubar.selectedPath$.pipe(
      takeUntil(this.unsubscribe),
      distinctUntilChanged())
      .subscribe( (path)  => {
        this.active =  this.matches(path) ;
        setTimeout(() => {
          this.tabindex = this.active ? 0 : -1;
          this.menubar.setActiveItem(path);
        });
      });

    this.menubar.activatedPath$.pipe(
      takeUntil(this.unsubscribe),
      distinctUntilChanged())
      .subscribe( (path)  => {
        const match = this.matches(path);
        setTimeout(() => this.tabindex = match ? 0 : -1);
      });
  }

  ngOnDestroy(): void {
    this.unsubscribe.next();
    this.unsubscribe.complete();
  }

  // this can probably only be achieved in nav-menubar
  // @HostBinding('attr.aria-haspopup') hasPopup = true;

  constructor(protected menubar: RufMenubarComponent, protected _elementRef?: ElementRef) {}

  @HostListener('click', ['$event']) onClick(event) {
    if (!event.cancelBubble) {
      if (this.selectable) {
        this.menubar.select(this.path, this.data, event);
      } else {
        // still change activated state since user clicked on the item
        this.menubar.activate(this.path);
      }
    }
  }

  focus() {
    if (this._elementRef) {
      this._elementRef.nativeElement.focus();
    }
  }

  matches(selectedPath) {
    if (selectedPath && this.path) {
      const pattern = new RegExp('^((\/)?' + this.path + ')(\/.*)?$');  // use word boundary to exact match
      if (selectedPath === this.path || pattern.test(selectedPath)) { // match with current menuitem and with parent menuitem
        return true;
      }
    }
    return false;
  }

}
