import {
  Input,
  Output,
  EventEmitter,
  Component,
  ViewEncapsulation,
  ChangeDetectionStrategy,
  ElementRef,
  AfterViewInit,
  QueryList,
  HostBinding,
  ViewChildren
} from '@angular/core';
import { Observable } from 'rxjs';
import { RufKeyboardNavigation, RufKeyCodes } from '../input/keyboard';
import { RufFocusableDirective } from '../utils/focusable';
import { RufBaseItem } from '../common/base-item';

export interface RufSitemapItem extends RufBaseItem {
  children: RufSitemapItem[];
  context?: string;
}

@Component({
  selector: 'ruf-sitemap',
  templateUrl: './sitemap.component.html',
  styleUrls: ['./sitemap.component.scss'],
  encapsulation: ViewEncapsulation.None,
  providers: [],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RufSitemapComponent implements AfterViewInit {
  @HostBinding('attr.role') role = 'menubar';

  @Input() items: Observable<RufSitemapItem[]>;
  @Output() select = new EventEmitter<RufSitemapItem>();
  @Input() direction = 'row';

  @ViewChildren(RufFocusableDirective) sitemapItems: QueryList<RufFocusableDirective>;

  private _keyboardNav: RufKeyboardNavigation;

  constructor(protected _elementRef: ElementRef) {}

  ngAfterViewInit() {
    if (this.sitemapItems.length > 0) {
      this.addNewKeyboardNav();
    }
    this.sitemapItems.changes.subscribe(changes => {
      if (this.sitemapItems.length > 0) {
        this.addNewKeyboardNav();
      }
    });
  }

  addNewKeyboardNav() {
    this._keyboardNav = new RufKeyboardNavigation();
    this._keyboardNav.setDirection(this.direction);
    this._keyboardNav.init(this._elementRef, this.sitemapItems);
    this._keyboardNav.addTabIndex(this.sitemapItems);
  }

  // emit the currently selected
  selectItem(sitemapItem: RufSitemapItem) {
    this.select.emit(sitemapItem);
  }

  onKey(event: KeyboardEvent, item: RufSitemapItem) {
    this._keyboardNav.onKeyDown(event);
    switch (event.keyCode) {
      case RufKeyCodes.ENTER:
      case RufKeyCodes.SPACE:
        this.selectItem(item);
        break;
    }
  }

  uniqueItem(index, item) {
    return item.path;
  }
}
