import {
  Input,
  Output,
  EventEmitter,
  Component,
  ViewEncapsulation,
  ChangeDetectionStrategy,
  HostBinding,
  HostListener,
  ViewChild
} from '@angular/core';
import { ENTER, SPACE, ESCAPE } from '@angular/cdk/keycodes';
import { MatMenuTrigger } from '@angular/material/menu';
import { RufTriggerTarget } from '../trigger/trigger.interface';

// FIXME: this component should be independent of dynamic-menubar. Consider common/menu-item.ts for common interface.
import { RufDynamicMenubarItem } from '../dynamic-menubar/dynamic-menubar-item';

@Component({
  selector: 'ruf-menu-label',
  templateUrl: './menu-label.component.html',
  styleUrls: ['./menu-label.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RufMenuLabelComponent implements RufTriggerTarget {
  @Input() label = '';
  @Input() icon = '';
  @Input() children: RufDynamicMenubarItem[];
  // on child menu toggle
  @Output() toggle = new EventEmitter<boolean>();

  @Output() select = new EventEmitter<RufDynamicMenubarItem>();
  @HostBinding('class.ruf-layout-row') rowLayout = true; // tells the internal labeled-icon that it is in a row.
  @ViewChild(MatMenuTrigger, {static: false}) trigger: MatMenuTrigger;

  @Input() selectedPath: string;

  @HostListener('click', ['$event'])
  handleClick(event) {
    // labels with children will not propagate the click event by default
    if (this.children && this.children.length > 0) {
      event.preventDefault();
      event.stopPropagation();
    }
  }

  hasPopup(): boolean {
    return this.children ? this.children.length > 0 : false;
  }

  onClose() {
    this.toggle.emit(false);
  }

  handleKeydown(event) {
    if (!this.children || this.children.length === 0) {
      return;
    }
    switch (event.keyCode) {
      case SPACE:
      case ENTER:
        this.trigger.openMenu();
        if (this.trigger.menu) {
          // This is needed for dynamic menubar since it uses setTimeout for selecting a tab.
          // This should be removed when we refactor dynamic menubar.
          setTimeout(() => {
            this.trigger.menu.focusFirstItem();
          });
        }
        event.preventDefault();
        event.stopPropagation();
        this.toggle.emit(true);
        break;
      case ESCAPE:
        this.trigger.closeMenu();
        this.toggle.emit(false);
        event.stopPropagation();
        break;
      default:
    }
  }

}
