import {
  ChangeDetectorRef,
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  Output,
  OnDestroy,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { Subscription } from 'rxjs';
import { RufShellIntl } from '../../utils/shell-intl';
import { RufKeyCodes } from './../../input/keyboard';

@Component({
  selector: 'ruf-megamenu-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RufMegamenuSearchComponent implements OnDestroy {
  private _intlChanges: Subscription;
  private _placeholder: string;
  private _clearSearchLabel: string;
  @Input()
  get placeholder() {
    return this._placeholder || this._intl.megamenusearch.placeholder;
  }
  set placeholder(value: string) {
    this._placeholder = value;
  }

  @Input()
  get clearSearchLabel() {
    return this._clearSearchLabel || this._intl.megamenusearch.aria.clearSearchLabel;
  }
  set clearSearchLabel(value: string) {
    this._clearSearchLabel = value;
  }

  @Output() filterValue = new EventEmitter<string>();
  @Output() inputFocus = new EventEmitter<string>();

  @ViewChild('filter', {static: false}) filter: ElementRef;

  inputFocusClass: boolean;

  constructor(private _intl: RufShellIntl, changeDetectorRef: ChangeDetectorRef) {
    this._intlChanges = _intl.changes.subscribe(() => changeDetectorRef.markForCheck());
  }

  clear($event) {
    this.filter.nativeElement.value = '';
    this.filterValue.emit('');
    $event.preventDefault();
    $event.stopPropagation();
  }

  handleKeyDown($event) {
    switch ($event.keyCode) {
      case RufKeyCodes.SPACE:
      case RufKeyCodes.ENTER:
        this.clear($event);
        break;
    }
  }

  ngOnDestroy() {
    this._intlChanges.unsubscribe();
  }
}
