import {
  SimpleChanges,
  ElementRef,
  ViewContainerRef,
  TemplateRef,
  OnDestroy,
  OnChanges,
  OnInit,
  Input,
  Directive, ChangeDetectorRef
} from '@angular/core';

import {
  MediaChange,
  BaseDirective,
  MediaMonitor,
  StyleUtils
} from '@angular/flex-layout';

const FALSY = ['false', false, 0];

@Directive({
  selector: `
    [rufIfXs],
    [rufIfGtXs],
    [rufIfSm],
    [rufIfGtSm],
    [rufIfMd],
    [rufIfGtMd],
    [rufIfLg],
    [rufIfGtLg],
    [rufIfXl]
  `
})
export class RufIfDirective extends BaseDirective implements OnInit, OnChanges, OnDestroy {
  private isAlreadyInserted = false;

  @Input('rufIfXs')
  set rufIfXs(val) {
    this._cacheInput('rufIfXs', val);
  }

  @Input('rufIfGtXs')
  set rufIfGtXs(val) {
    this._cacheInput('rufIfGtXs', val);
  }

  @Input('rufIfSm')
  set rufIfSm(val) {
    this._cacheInput('rufIfSm', val);
  }

  @Input('rufIfGtSm')
  set rufIfGtSm(val) {
    this._cacheInput('rufIfGtSm', val);
  }

  @Input('rufIfMd')
  set rufIfMd(val) {
    this._cacheInput('rufIfMd', val);
  }

  @Input('rufIfGtMd')
  set rufIfGtMd(val) {
    this._cacheInput('rufIfGtMd', val);
  }

  @Input('rufIfLg')
  set rufIfLg(val) {
    this._cacheInput('rufIfLg', val);
  }

  @Input('rufIfGtLg')
  set rufIfGtLg(val) {
    this._cacheInput('rufIfGtLg', val);
  }

  @Input('rufIfXl')
  set rufIfXl(val) {
    this._cacheInput('rufIfXl', val);
  }

  constructor(
    private templateRef: TemplateRef<any>,
    private viewContainer: ViewContainerRef,
    monitor: MediaMonitor,
    protected elRef: ElementRef,
    protected styleUtils: StyleUtils,
    protected changeDetectorRef: ChangeDetectorRef
  ) {
    super(monitor, elRef, styleUtils);
  }

  protected _getDisplayStyle(): string {
    // this is not used for rufIf, only prevents error from being throw as BaseDirective invokes it
    return 'block';
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['rufIf'] != null || this._mqActivation) {
      this._updateWithValue();
    }
  }

  ngOnInit() {
    this._listenForMediaQueryChanges('rufIf', false, (changes: MediaChange) => {
      this._updateWithValue(changes.value);
      this.changeDetectorRef.markForCheck();
    });
    this._updateWithValue();
  }

  ngOnDestroy() {
    super.ngOnDestroy();
  }

  private _updateWithValue(value?: string | number | boolean) {
    value = value || this._getDefaultVal('rufIf', false);
    if (this._mqActivation) {
      value = this._mqActivation.activatedInput;
    }

    const shouldShow = this._validateTruthy(value);
    this._update(shouldShow);
  }

  // Insert or remove the template
  private _update(value) {
    if (value && !this.isAlreadyInserted) {
      this.viewContainer.createEmbeddedView(this.templateRef);
      this.isAlreadyInserted = true;
    } else if (!value && this.isAlreadyInserted) {
      this.viewContainer.clear();
      this.isAlreadyInserted = false;
    }
  }

  private _validateTruthy(value) {
    return FALSY.indexOf(value) === -1;
  }
}
