import {
  ChangeDetectorRef,
  ChangeDetectionStrategy,
  Component,
  Directive,
  ViewEncapsulation,
  Input,
  OnDestroy
} from '@angular/core';

import { trigger, state, style, transition, animate } from '@angular/animations';
import { Subscription } from 'rxjs';
import { RufShellIntl } from '../utils/shell-intl';

@Directive({
  selector: 'ruf-card-controls' // tslint:disable-line
})
export class RufCardControlsDirective {}

@Directive({
  selector: 'ruf-card-subtitle' // tslint:disable-line
})
export class RufCardSubtitleDirective {}

@Component({
  selector: 'ruf-card',
  templateUrl: 'card.component.html',
  styleUrls: ['card.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  animations: [
    trigger('expanded', [
      state(
        'false',
        style({
          height: 0,
          visibility: 'hidden'
        })
      ),
      state(
        'true',
        style({
          height: '*',
          visibility: 'visible'
        })
      ),
      transition('false => true', animate('300ms ease-in-out')),
      transition('true => false', animate('300ms ease-in-out'))
    ])
  ]
})
export class RufCardComponent implements OnDestroy {
  private _intlChanges: Subscription;
  private _expandLabel: string;
  private _collapseLabel: string;
  @Input() title: '';
  @Input() expandable = false;
  @Input() expanded = true;

  @Input()
  get expandLabel() {
    return this._expandLabel || this._intl.card.aria.expandLabel;
  }
  set expandLabel(value: string) {
    this._expandLabel = value;
  }

  @Input()
  get collapseLabel() {
    return this._collapseLabel || this._intl.card.aria.collapseLabel;
  }
  set collapseLabel(value: string) {
    this._collapseLabel = value;
  }

  get toggleLabel() {
    return this.expanded ? this.collapseLabel : this.expandLabel;
  }

  constructor(private _intl: RufShellIntl, changeDetectorRef: ChangeDetectorRef) {
    this._intlChanges = _intl.changes.subscribe(() => changeDetectorRef.markForCheck());
  }

  ngOnDestroy() {
    this._intlChanges.unsubscribe();
  }
}
