import { Directive, HostBinding, Input, OnChanges, SimpleChanges, Renderer2, ElementRef } from '@angular/core';

export const RUF_PADDING_VALUES = ['none', 'auto', 'small', 'medium', 'large'];

@Directive({
  selector: `
    [rufPadding],
    [rufPaddingVertical],
    [rufPaddingHorizontal],
    [rufPaddingTop],
    [rufPaddingRight],
    [rufPaddingBottom],
    [rufPaddingLeft]
  `,
  exportAs: 'rufPadding'
})
export class RufPaddingDirective implements OnChanges {
  private classPrefix = 'ruf-padding';

  private _positions = {
    rufPadding: ['-'],
    rufPaddingVertical: ['-top-', '-bottom-'],
    rufPaddingHorizontal: ['-left-', '-right-'],
    rufPaddingTop: ['-top-'],
    rufPaddingRight: ['-right-'],
    rufPaddingBottom: ['-bottom-'],
    rufPaddingLeft: ['-left-']
  };

  @Input() rufPadding = '';
  @Input() rufPaddingVertical = '';
  @Input() rufPaddingHorizontal = '';
  @Input() rufPaddingTop = '';
  @Input() rufPaddingRight = '';
  @Input() rufPaddingBottom = '';
  @Input() rufPaddingLeft = '';

  @HostBinding('class.ruf-padding') padding = true;

  constructor(private el: ElementRef, private renderer: Renderer2) {}

  ngOnChanges(changes: SimpleChanges) {
    Object.keys(changes).forEach(key => {
      this.update(key, changes[key]);
    });
  }

  private update(key, change) {
    const previous = this._validateValue(change.previousValue);
    const current = this._validateValue(change.currentValue);
    const positions = this._positions[key];

    positions.forEach(pos => {
      // remove
      this.renderer.removeClass(this.el.nativeElement, this.classPrefix + pos + previous);
      // add
      this.renderer.addClass(this.el.nativeElement, this.classPrefix + pos + current);
    });
  }

  private _validateValue(val) {
    if (RUF_PADDING_VALUES.indexOf(val) !== -1) {
      return val;
    }
    return 'medium';
  }
}
