import { LOCALE_ID, ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RufLocaleService } from './locale.service';
import { RufDatePipe } from './date.pipe';
import { RufCurrencyPipe } from './currency.pipe';

@NgModule({
  imports: [CommonModule],
  declarations: [RufDatePipe, RufCurrencyPipe],
  exports: [RufDatePipe, RufCurrencyPipe]
})
export class RufI18nModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: RufI18nModule,
      providers: [{ provide: LOCALE_ID, useFactory: localeFactory, deps: [RufLocaleService] }, RufLocaleService]
    };
  }
}

export function localeFactory(localeService: RufLocaleService) {
  return localeService.locale;
}
