import { RufSidemenuComponent } from './../sidemenu/sidemenu.component';
import { RufSearchService } from './menu-search.service';
import { RufSearchItem } from './search/searchitem.interface';
import {
  ChangeDetectorRef,
  Component,
  Input,
  Output,
  EventEmitter,
  ViewEncapsulation,
  OnInit,
  OnDestroy,
  AfterViewInit,
  ViewChild,
  ChangeDetectionStrategy,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import { Observable, Subject, merge, of ,  Subscription } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  map,
  startWith,
  switchMap,
  withLatestFrom
} from 'rxjs/operators';
import { RufShellIntl } from '../utils/shell-intl';

@Component({
  selector: 'ruf-megamenu',
  templateUrl: './megamenu.component.html',
  styleUrls: ['./megamenu.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [RufSearchService]
})
export class RufMegamenuComponent implements AfterViewInit, OnInit, OnChanges, OnDestroy {
  private _intlChanges: Subscription;
  private _searchPlaceholder: string;
  private _clearSearchAriaLabel: string;
  @Input()
  get searchPlaceholder() {
    return this._searchPlaceholder || this._intl.megamenu.placeholder;
  }
  set searchPlaceholder(value: string) {
    this._searchPlaceholder = value;
  }

  @Input()
  get clearSearchAriaLabel() {
    return this._clearSearchAriaLabel || this._intl.megamenu.aria.clearSearchLabel;
  }
  set clearSearchAriaLabel(value: string) {
    this._clearSearchAriaLabel = value;
  }

  @Input() items: RufSearchItem[] = [];
  @Output() select = new EventEmitter<RufSearchItem>();

  @ViewChild('sidemenu', {static: true}) sidemenu: RufSidemenuComponent;

  // Subjects: denotes user interaction streams
  // search related properties
  searchTerms$ = new Subject<string>(); // stream of search terms
  // sidemenu select event stream
  selectedSidemenuItems$ = new Subject<any>(); // stream of selected items (same type has this.items)
  // merge search results and selected menu item's children into a single stream
  sitemapItems$: Observable<RufSearchItem[]>; // use same type has this.items

  private preparedSearchableItems: (term: string) => RufSearchItem[] = (term) => [];

  constructor (private searchService: RufSearchService, private _intl: RufShellIntl, changeDetectorRef: ChangeDetectorRef) {
    this._intlChanges = _intl.changes.subscribe(() => changeDetectorRef.markForCheck());
  }

  ngOnInit() {
    // map search terms to a search results stream ready to be consumed by sitemap
    const searchResults$ = this.searchTerms$.pipe(
      distinctUntilChanged(),
      debounceTime(400), // don't search unless user stopped typing for 400ms
      switchMap(term => this.search(term)), // ignore already triggered searches.
      startWith([])
      ); // start with empty results

    // map sidemenu select event to selected item's children array
    const selectedItemChildren$ = this.selectedSidemenuItems$.pipe(
      withLatestFrom(searchResults$), // combine each event with the latest of the searchResults$ stream
      map(([item, searchResults]) => {
        if (!item) {
          return searchResults || [];
        }
        // map to the selected item's children or an empty array.
        return item.data || [];
      }));

    // sitemap data stream is a merge of search results and selected item's children streams
    this.sitemapItems$ = merge(
      selectedItemChildren$,
      searchResults$
    );
  }

  ngOnChanges(changes: SimpleChanges) {
    // when any changes to the items occur, re-prepare the search
    if (changes.items && changes.items.currentValue) {
      this.preparedSearchableItems = this.searchService.prepareSearch(changes.items.currentValue);
    }
  }

  ngAfterViewInit() {
    // once the sidemenu is created (after view init), select first sidemenu item
    setTimeout(() => {
      if (this.items[0]) {
        this.sidemenu.select(this.items[0].path, this.items[0].children);
      }
    });
  }

  private search(term: string): Observable<RufSearchItem[]> {
    let results = this.items;
    // if no prepared searchable data is available we just return everything
    if (this.preparedSearchableItems) {
      // now we process the searching of the data
      results = this.preparedSearchableItems(term);
    }
    if (!term || term === '') {
      results = [];
    }
    // return observable of results using same type has this.item
    return of(results);
  }

   ngOnDestroy() {
    this._intlChanges.unsubscribe();
  }
}
