import {
  ChangeDetectorRef,
  Component,
  Input,
  ViewEncapsulation,
  ElementRef,
  ChangeDetectionStrategy,
  Directive,
  AfterContentInit,
  ContentChildren,
  QueryList,
  HostListener,
  OnDestroy
} from '@angular/core';

import { RufKeyboardNavigation, RufKeyCodes } from '../input/keyboard';
import { RufFocusableDirective } from '../utils/focusable';
import { Subscription } from 'rxjs';
import { RufShellIntl } from '../utils/shell-intl';
import { RufMixinBase, RufCanColor } from '../common/color/color';

@Directive({
  selector: 'ruf-toolbar-row' // tslint:disable-line
})
export class RufToolbarRowDirective {}

@Component({
  selector: 'ruf-toolbar',
  templateUrl: './toolbar.component.html',
  styleUrls: ['./toolbar.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RufToolbarComponent extends RufMixinBase implements AfterContentInit, OnDestroy, RufCanColor {
  private _intlChanges: Subscription;
  private _color: string;
  private _keyboardNav: RufKeyboardNavigation;
  private _ariaLabel: string;

  /* tslint:disable:no-input-rename */
  @Input() color;

  /* tslint:disable:no-input-rename */
  @Input()
  set ariaLabel(label: string) {
    this._ariaLabel = label;
  }

  get ariaLabel() {
    return  this._ariaLabel || this._intl.toolbar.aria.label;
  }


  private children = [];

  @ContentChildren(RufFocusableDirective, { descendants: true })
  toolbarItems: QueryList<RufFocusableDirective>;

  constructor(public _elementRef: ElementRef,
              private _intl: RufShellIntl,
              changeDetectorRef: ChangeDetectorRef
              ) {
      super(_elementRef);
      this._intlChanges = _intl.changes.subscribe(() => {
        changeDetectorRef.markForCheck();
      });
   }

  ngAfterContentInit() {
    this._keyboardNav = new RufKeyboardNavigation();
    this._keyboardNav.init(this._elementRef, this.toolbarItems);
    this._keyboardNav.addTabIndex(this.toolbarItems);
  }

  @HostListener('keydown', ['$event'])
  onKey(event: KeyboardEvent) {
    switch (event.keyCode) {
      case RufKeyCodes.SPACE:
      case RufKeyCodes.ENTER:
        event.stopPropagation();
        break;
      default:
        this._keyboardNav.onKeyDown(event);
    }
  }

  ngOnDestroy() {
    this._intlChanges.unsubscribe();
  }

}
