import { Injectable, ComponentRef } from '@angular/core';
import { ActivatedRouteSnapshot, RouterOutlet } from '@angular/router';

@Injectable()
export class RufRouterUtilService {
  private _routeComp: { [key: string]: ComponentRef<any> } = {};
  private _lastActivatedPath: string;
  private _lastActivatedOutlet: RouterOutlet;

  set lastActivatedPath(path: string) {
    this._lastActivatedPath = path;
  }

  get lastActivatedPath(): string {
    return this._lastActivatedPath;
  }

  set lastActivatedOutlet(outlet: RouterOutlet) {
    this._lastActivatedOutlet = outlet;
  }

  get lastActivatedOutlet(): RouterOutlet {
    return this._lastActivatedOutlet;
  }

  setComponentRef(path: string, componentRef: ComponentRef<any>) {
    this._routeComp[path] = componentRef;
  }

  getComponentRef(path): ComponentRef<any> {
    return this._routeComp[path];
  }

  fullPath(snapshot: ActivatedRouteSnapshot): string {
    let path = '';
    const rootElems = snapshot.pathFromRoot;
    rootElems.forEach(function(child) {
      const urlArray = child.url;
      urlArray.forEach(function(url) {
        path += `/${url.path}`;
      });
    });
    return path;
  }

  getLeafNodePath(route: ActivatedRouteSnapshot): string {
    let path;
    // if (!route.routeConfig.loadChildren) {
    // If !route.component, this is a parent route. Return path of the leaf node in this case.
    if (!route.component) {
      const leafNode = this.getLeafNode(route);
      path = this.fullPath(leafNode);
    } else {
      path = this.fullPath(route);
    }
    // }
    return path;
  }

  getLeafNode(route: any): any {
    if (!route.children || route.children.length === 0) {
      return route;
    }
    return this.getLeafNode(route.firstChild || route.children[0]);
  }
}
