import {
  Component,
  ViewChild,
  ElementRef,
  Input,
  Output,
  EventEmitter,
  ViewEncapsulation,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  OnDestroy,
  AfterViewChecked
} from '@angular/core';
import {RufShellIntl} from '../utils/shell-intl';
import {Subscription,  BehaviorSubject} from 'rxjs';


@Component({
  selector: 'ruf-ellipsis',
  templateUrl: './ellipsis.component.html',
  styleUrls: ['./ellipsis.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class RufEllipsisComponent implements AfterViewChecked, OnDestroy {
  private _intlChanges: Subscription;

  showReadMore$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  @Input() text: string;
  @Input() lines = 5;
  @Input() lineHeight = 1;
  private _label: string;
  private _ariaLabel: string;


  @Input()
  get label() {
    return this._label || this._intl.ellipsis.label || '...';
  }
  set label(value: string) {
    this._label = value;
  }

  @Input()
  get ariaLabel() {
    return this._ariaLabel || this._intl.ellipsis.aria.label;
  }
  set ariaLabel(value: string) {
    this._ariaLabel = value;
  }

  @ViewChild('ellipsisContainer', {static: false}) ellipsisContainer: ElementRef;

  @Output() expand = new EventEmitter();

  constructor(private _intl: RufShellIntl, private changeDetectorRef: ChangeDetectorRef) {
    this._intlChanges = _intl.changes.subscribe(() => this.changeDetectorRef.markForCheck());
  }

  checkReadMore() {
   if (this.text && this.ellipsisContainer.nativeElement.offsetHeight < this.ellipsisContainer.nativeElement.scrollHeight) {
      this.showReadMore$.next(true);
    } else {
      this.showReadMore$.next(false);
    }
  }

  ngAfterViewChecked(): void {
    this.checkReadMore() ;
    this.changeDetectorRef.detectChanges();
  }

  ngOnDestroy() {
    this._intlChanges.unsubscribe();
  }
}
